<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/store/category" :type='$route.query.type'  >
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt collapse='true'>
        <li>带 * 为必填项</li>
      </cms-prompt>
     <el-form ref="form" :rules="saveRules" :model="formData" label-width="210px"  @submit.native.prevent>
         <el-form-item label="分类名称：" prop="cateName" :error='errorMessage.cateName'>
          <el-input v-model="formData.cateName" maxlength="50"></el-input>
        </el-form-item>
         <el-form-item label="上级分类：" prop="parentId">
           <el-cascader :options="options" :props="props" :change-on-select='true' v-model="formData.parentIds" placeholder="请选择分类">
          </el-cascader>
          <span class="gray">不选择，默认为顶级分类</span>
        </el-form-item>
         <el-form-item label="是否显示：" prop="isShow" :error='errorMessage.isShow'>
          <cms-switch v-model="formData.isShow"></cms-switch>
        </el-form-item>
         <el-form-item label="是否显示店铺导航栏：" prop="isTop" :error='errorMessage.isTop'>
          <cms-switch v-model="formData.isTop"></cms-switch>
          <span class="gray">设置该分类是否显示在店铺分类导航中</span>
        </el-form-item>
         <el-form-item label="分类页标题：" prop="title">
          <el-input v-model="formData.title" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="分类页关键字：" prop="keywords">
          <el-input v-model="formData.keywords" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="分类页描述：" prop="description">
          <el-input type='textarea' :rows='4' v-model="formData.description" maxlength="200"></el-input>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button  @click="save(false)" v-color >保存并新增</cms-button>
        <cms-button @click="save(true)"  v-color >保存</cms-button>
        <cms-button type='rest' @click="reset">重置</cms-button>
      
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { savecategory, storeCategoryTree } from '@/api/shop/shopManage.js'
var defaultData = {
  'cateName': '',
  'parentId': '',
  'isShow': true,
  'isTop': true,
  'title': '',
  'keywords': '',
  'description': '',
  'sortNum': 10,
  'storeId': '',
  parentIds: []
}
export default {
  mixins: [form],
  data() {
    return {

      options: [],
      props: {
        value: 'id',
        label: 'cateName',
        children: 'childs',
        disabled: 'isAuth'
      },

      formData: Object.assign({}, defaultData),
      saveRules: {// 新增规则
        cateName: [rules.required('请填写分类名称 ')],
        isShow: [rules.required()],
        isTop: [rules.required()]
      }
    }
  },
  methods: {
    getMenuTree() {
      // 获取菜单节点
      storeCategoryTree().then(res => {
        const root = [{ id: 0, cateName: '顶级分类' }]
        this.options = root.concat(res.data)
      })
    },
    getAll() {
      this.getMenuTree()
    },
    save(state) { // 添加
      const params = this.$cms.deepCopy(this.formData)
      params.parentId = params.parentIds[params.parentIds.length - 1]
      delete params.parentIds// 处理掉参数
      if (params.parentId === 0) {
        params.parentId = ''
      }
      this.saveForm(savecategory, params, state, '/store/category', this.$route.query.type)
    },
    reset() {
      window.clearInterval(this.timer)
      this.autoFocus()
      this.$refs.form.resetFields()
      this.destroyed()
      this.formData.parentIds = [0]
    }
  },
  created() {
    this.formData.parentIds = this.$cms.arrayToNumber(this.$route.query.nodeIds)

    this.getAll()
  }
}
</script>




